export const statusText = (status) => {
  switch (status) {
    case "done":
      return {
        name: "",
        style: { color: "#00cccc" },
      };
    case "pending":
      return {
        name: "",
        style: { color: "#F2F2F3" },
      };
    case "created":
      return {
        name: "",
        style: { color: "#F2F2F3" },
      };
    case "cancel":
      return {
        name: "",
        style: { color: "red" },
      };
    default:
      return {
        name: " ",
        style: { color: "#F2F2F3" },
      };
  }
};
